
import { mapGetters, mapActions } from 'vuex'
import { defineComponent } from 'vue';

export default defineComponent({
  name: 'PrevueImages',
  computed: {
      ...mapGetters(['allCollage'])
  },
  methods: {
      ...mapActions(['fetchCollage'])
  },
  async mounted() {
      this.fetchCollage()
  },
});
