
    import {defineComponent} from 'vue';

    export default defineComponent({
        props: ['contacts', 'about', 'catalog'],
        name: 'Header',
        data() {
            return {
                isMenuOpen: false
            }
        },
        methods: {
            isShowHandlew() {
                console.log("click")
                this.isMenuOpen = this.isMenuOpen === false ? true : false
            },
            goTo(path: string) {
                console.log(path)
                this.$router.push({path})
            },
        },

    });
